import SidebarElement from '../../Components/SidebarElements';

import {
  Logo,
  DashboardImg,
  Discount,
  Home,
  LogOut,
  Message,
  Notification,
  Setting
} from '../../assets/images/icons';

import './main.scss';

const Sidebar = () => {
  return (
    <div className="sidebar">
      <div className="Logo">
        <SidebarElement goTo='/' itemLogo={<Logo />} />
      </div>

      <SidebarElement goTo='/' itemLogo={<Home />} />
      <SidebarElement goTo='/Dashboard' itemLogo={<DashboardImg />} />
      <SidebarElement goTo='/' itemLogo={<Discount />} />
      <SidebarElement goTo='/' itemLogo={<Message />} />
      <SidebarElement goTo='/' itemLogo={<Notification />} />
      <SidebarElement goTo='/Settings' itemLogo={<Setting />} />
      <div className="log-out">
        <SidebarElement goTo='/' itemLogo={<LogOut />} />
      </div>
    </div>
  )
}

export default Sidebar;