import { Link } from 'react-router-dom';

import './Card.scss';

const Card = ({ cardImg, title, price, bowls }) => {
  return(
    <Link className="card">
      <img src={cardImg} alt="" className="card-img" />
      <h3 className="card-title">{title}</h3>
      <h4 className="card-price">${price}</h4>
      <span className="card-bowl">{bowls} Bowls available</span>
    </Link>
  )
}

export default Card;