import './RightSidebar.scss';

import PaymentMethods from '../../Components/PaymentMethods/PaymentMethod';

import { Card, Paypal, Wallet} from '../../assets/images/icons';

const RightSidebar = () => {
  return(
    <div className="RigthSidebar">
      <div className="top">
        <h2 className="title">Payment</h2>
        <span className="subtitle">3 payment method available</span>
      </div>

    <div className="middle">
        <h3 className="title">Payment Method</h3>

        <div className="middle-block">
          <PaymentMethods title="Credit Card" ico={ <Card /> }/>
          <PaymentMethods title="Paypal" ico={<Paypal />}/>
          <PaymentMethods title="Cash" ico={<Wallet />}/>
        </div>

        <div className="holder">
          <form action="https://echo.htmlacademy.ru">
            <label htmlFor="name">Cardholder Name</label>
            <input type="text" name="Cardholder name" id="name" placeholder="Levi Ackerman" className="holder-inp"/>
            
            <label htmlFor="card-number">Card Number</label>
            <input type="number" name="Card number" id="card-number" placeholder="2564 1421 0897 1244" className="holder-inp"/>

            <div className="middle">
              <label htmlFor="date">Expiration Date
              <input type="date" name="Expiration date" id="date" placeholder="02/2022" className="holder-inp" />
              </label>

              <label htmlFor="cvv">CVV
              <input type="password" id="cvv" name="CVV" placeholder="123" className="holder-inp" />
              </label>
            </div>

            <div className="bottom">
              <div>
                <label htmlFor="order">Order Type</label>
                <select name="order type" id="order" className="holder-inp">
                  <option value="dine in">Dine In</option>
                  <option value="dine in">Dine In</option>
                  <option value="dine in">Dine In</option>
                  <option value="dine in">Dine In</option>
                </select>
              </div>

              <label htmlFor="table">Table no.
              <input type="number" id="table" name="Table no." placeholder="140" className="holder-inp" />
              </label>
            </div>

          </form>
        </div>
    </div>

    <div className="bottom">
        <button className="bottom-primary">Cancel</button>
        <button className="bottom-secondary">Confirm Payment</button>
    </div>

    </div>
  )
}

export default RightSidebar;