import './DashRightCard.scss';

const DashRightCard = ({ img, title, dishCount }) => {
  return(
    <div className="dash-right-card">
      <img src={img} alt=""/>

      <div className="info">
        <h3 className="title">{title}</h3>
        <span className="dishCount">{dishCount}</span>
      </div>

    </div>
  )
}

export default DashRightCard;