import './MenCard.scss';

const MenCard = ({ avatar, name, menu, totalPayment, status }) => {
  return(
    <div className="mencard">
      <div className="fcol">
        <img src={avatar} alt=""/>
        <h3 className="name">{name}</h3>
      </div>

      <h3 className="mencard-menu">{menu}</h3>

      <span className="total-payment">{totalPayment}</span>

      <div className={`status ${status}`}>
        {status}
      </div>
    </div>
  )
}

export default MenCard;