import { Trash } from '../../assets/images/icons';

import './RightCard.scss';

const RightCard = ({ cardImg, title, count, price, allPrice }) => {
  return (
    <div className="right-card">
      <div className="top">
        <div className="left">
          <img src={cardImg} alt="" className="left-img" />
          <div className="left-right">
            <h3 className="left-right-title">{title}</h3>
            <span className="left-right-price">{price}</span>
          </div>
        </div>
        <div className="right">
          <div className="count">{count}</div>
          <div className="all-price">{allPrice}</div>
        </div>
      </div>
      <div className="bottom">
        <div className="bottom-right">
          <input type="text" placeholder="Order Note..." className="bottom-inp" />
        </div>
        <button className="trash-btn"> <Trash /> </button>
      </div>
    </div>
  )
}

export default RightCard;